<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <!-- 按钮组插槽 -->
    <template #btnGroup>
      <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
      </g-btn-group>
    </template>
    <!-- g-ndo-table插槽 -->
    <template #table>
      <g-ndo-table
        :id="id"
        :handName="'电子签名需求'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-ndo-table>
    </template>
    <!-- 详细内容插槽 -->
    <template #content>
      <create-or-edit-e-sign-requirement
        :ndoEditParams="ndoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick($event)"
        v-if="isShowEdit"
      ></create-or-edit-e-sign-requirement>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import CreateOrEditESignRequirement from "./create-or-edit-e-sign-requirement/create-or-edit-e-sign-requirement.vue";
import { GNdoPageBase } from "@/utils";

export default {
  name: "ESignRequirement",
  mixins: [GNdoPageBase],
  components: {
    CreateOrEditESignRequirement,
  },

  data() {
    return {
      isTableLoading: false,
      filterText: "",
    };
  },

  created() {
    this.getBtnList();
  },

  methods: {
    fetchData(request) {
      this.loading = true;
      setTimeout(() => {
        request.finishedCallback();
        // 模拟数据
        let dataList = [];
        for (let i = 0; i < 25; i++) {
          dataList[i] = {
            id: i + 1,
            requirementCode: "ESR" + (1000 + i),
            name: "电子签名需求" + (i + 1),
            service: ["审批流程", "操作确认", "质量检查"][i % 3],
            roleGroup: ["管理组", "操作组", "质检组"][i % 3],
            reason: ["操作确认", "审核确认", "质量确认"][i % 3],
            status: ["启用", "禁用"][i % 2],
            createTime: "2024-" + ((i % 12) + 1) + "-" + ((i % 28) + 1),
          };
        }

        if (this.filterText) {
          dataList = dataList.filter((item) => {
            return (
              item.requirementCode.indexOf(this.filterText) > -1 ||
              item.name.indexOf(this.filterText) > -1
            );
          });
        }

        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped></style>