<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <a-form-model-item label="需求编码" prop="requirementCode">
              <a-input
                v-model="entity.requirementCode"
                placeholder="请输入需求编码"
                :disabled="!!id"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="需求名称" prop="requirementName">
              <a-input
                v-model="entity.requirementName"
                placeholder="请输入需求名称"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="电子签名服务" prop="service">
              <a-select
                v-model="entity.service"
                placeholder="请选择电子签名服务"
              >
                <a-select-option value="审批流程">审批流程</a-select-option>
                <a-select-option value="操作确认">操作确认</a-select-option>
                <a-select-option value="质量检查">质量检查</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="角色组" prop="roleGroup">
              <a-select
                v-model="entity.roleGroup"
                placeholder="请选择角色组"
              >
                <a-select-option value="管理组">管理组</a-select-option>
                <a-select-option value="操作组">操作组</a-select-option>
                <a-select-option value="质检组">质检组</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :sm="6">
            <a-form-model-item label="签名原因" prop="reason">
              <a-select
                v-model="entity.reason"
                placeholder="请选择签名原因"
              >
                <a-select-option value="操作确认">操作确认</a-select-option>
                <a-select-option value="审核确认">审核确认</a-select-option>
                <a-select-option value="质量确认">质量确认</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="状态" prop="status">
              <a-select
                v-model="entity.status"
                placeholder="请选择状态"
              >
                <a-select-option value="启用">启用</a-select-option>
                <a-select-option value="禁用">禁用</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :sm="24">
            <a-form-model-item label="描述" prop="description">
              <a-textarea
                v-model="entity.description"
                :rows="4"
                placeholder="请输入描述"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <g-collapse headerName="其他信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <a-form-model-item label="创建人">
                <span>{{ entity.creator || 'admin' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="创建时间">
                <span>{{ entity.createTime || '2024-01-01 12:00:00' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="最后修改人">
                <span>{{ entity.modifier || 'admin' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="最后修改时间">
                <span>{{ entity.modifyTime || '2024-01-01 12:00:00' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditESignRequirement",
  mixins: [GNdoPageEditBase],

  data() {
    return {
      entity: {
        requirementCode: "",
        requirementName: "",
        service: undefined,
        roleGroup: undefined,
        reason: undefined,
        status: "启用",
        description: "",
        creator: "",
        createTime: "",
        modifier: "",
        modifyTime: "",
      },
      rules: {
        requirementCode: [
          { required: true, message: "需求编码不能为空", trigger: "change" },
          {
            pattern: /^[A-Z0-9]+$/,
            message: "需求编码只能包含大写字母和数字",
            trigger: "change",
          },
        ],
        requirementName: [
          { required: true, message: "需求名称不能为空", trigger: "change" },
          {
            pattern: /^[^\s]+(\s+[^\s]+)*$/,
            message: "名称不能包含前后空格",
            trigger: "change",
          },
        ],
        service: [
          { required: true, message: "请选择电子签名服务", trigger: "change" },
        ],
        roleGroup: [
          { required: true, message: "请选择角色组", trigger: "change" },
        ],
        reason: [
          { required: true, message: "请选择签名原因", trigger: "change" },
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" },
        ],
      },
    };
  },

  methods: {
    init() {
      this.loading = true;
      if (this.id) {
        // 模拟请求
        setTimeout(() => {
          this.loading = false;
          this.entity = {
            requirementCode: "ESR1001",
            requirementName: "测试电子签名需求",
            service: "审批流程",
            roleGroup: "管理组",
            reason: "操作确认",
            status: "启用",
            description: "这是一个测试电子签名需求",
            creator: "admin",
            createTime: "2024-01-01 12:00:00",
            modifier: "admin",
            modifyTime: "2024-01-01 12:00:00",
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },

    resetFormData() {
      this.entity = {
        requirementCode: "",
        requirementName: "",
        service: undefined,
        roleGroup: undefined,
        reason: undefined,
        status: "启用",
        description: "",
        creator: "",
        createTime: "",
        modifier: "",
        modifyTime: "",
      };
    },

    async save() {
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.requirementName);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },

    inChange(e) {
      console.log(e.target.value);
    },
  },
};
</script>

<style lang="scss" scoped>
.card {
  background: #fff;
  padding: 24px;
  margin-bottom: 24px;
}
</style>